.nav-container {
    background-color: var(--Navbar);
    background: solid;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 2.5vh 2.5vw;
    align-items: center;
    justify-content: center;
}

.nav-icon {
    display: none;
}

.nav-item,
.logo_name {
    text-decoration: none;
    color: var(--IconesLinks);
    padding: 0px 5px;
    border-radius: 5px;
    margin: 5px;
}

.nav-item:hover {
    color: var(--Hover);
    background-color: var(--Texto);
    padding: 5px;
}

.logo {
    height: 40px;
    align-items: center;
    justify-content: center;
    display: flex;
}

.logo_name {
    font-weight: 700;
    font-size: 1.5vw;
}

.logo_icon {
    height: 100%;
    overflow: hidden;
    object-fit: cover;
    padding-bottom: 5px;
}

@media (max-width: 992px) {
    .nav-container{
        justify-content: space-between;
    }
    .logo_name{
        font-size:20px;
    }
    .nav-icon {
        display: inline;
    }

    .nav-container.responsive .dropdown-content {
        display: flex;
      }

      .nav-links{
        display: none;
        position: absolute;
        top: 50px;
        right: 0;
        background-color: var(--Navbar);
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.678);
        flex-direction: column;
        width: 200px;
        border-radius: 4px;
        z-index: 10;
      }

      .nav-links > nav-link {
            padding: 12px;
            text-align: left;
            width: 100%;
            box-sizing: border-box;
      }
}